import { produce } from 'immer';
import * as actions from './actions';
import Cookies from 'js-cookie';
import { SessionKey } from '../assets/constants';

export const MeetingViews = {
  Join: '/join',
  Home: '/',
}

// const UserInfo = {
//   name: "pig heaven",
//   email: "pig.heaven@good.life",
//   avatar: "https://zoomdev.us/static/95763/image/new/home/icon_4.png"
// }

// let meetingHistoryEntry = {
//   meetingId: "123456789",
//   meetingTopic: "the quick brown fox jumps over the lazy dog"
// }

const initialState =  {
    isDuringMeeting: false,
    meetingCurrentView: MeetingViews.Home,
    userSessionCredential: Cookies.get(SessionKey) || "",
    userInfo: null,
    lastJoinName: "",
    meetingHistoryList: [],
};

const rootReducer = produce( (state = initialState, action) => {
    switch (action.type) {
        case actions.SET_USERINFO:
            state.userInfo = action.payload;
            break;
        case actions.IS_DURING_MEETING:
            state.isDuringMeeting = action.payload;
            break;
        case actions.SET_CURRENT_VIEW:
            state.meetingCurrentView = action.payload;
            break;
        case actions.SET_USER_SESSION_CREDENTIAL:
            if(!action.payload) {
              state.userInfo = null;
            }
            state.userSessionCredential = action.payload;
            break;
        default:
            return state;
    }
});

export default rootReducer;
