/**
 * this is shown if user hasn't loged in.
 * he can just 
 *  1. login
 *  2. join a meeting
 */

import React from 'react'
import { connect } from 'react-redux';
import './index.scss';
import { setCurrentView } from 'store/actions';
import { MeetingViews } from 'store/reducer';
import { getSigninPath, getStartMeetingPath } from '../../assets/constants'
import {setUserSessionCredential, setUserInfo} from '../../store/actions';
function Home(props) {
    let {
      userInfo,
      userSessionCredential,
      signIn,
      startMeeting,
      joinMeeting,
      logout
    } = props;
    console.log(userInfo, "in Home")
    return (
        <div className="home">
            {
                (userSessionCredential &&  userInfo) && (
                    <div className="home-header">
                        <img src={userInfo.avatar || ""} className="home-header-avatar" alt="user avatar" />
                        <span className="home-header-name">{userInfo.displayName}</span>
                        <span className="home-header-logout" onClick={logout}>Logout</span>
                    </div>
                )
            }
            <div className="home-logo"></div>

            <div className="btns-home">
                {
                    userSessionCredential && (
                        <button
                            className="btn btn-block btn-home-start"
                            onClick={startMeeting}
                        >Start Meeting</button>
                    )
                }

                <button
                    className="btn btn-block btn-home-join"
                    onClick={joinMeeting}
                >Join Meeting</button>

                {
                  !userSessionCredential && (
                    <button
                        className="btn btn-block btn-home-signin"
                        onClick={signIn}
                    >Sign In</button>
                  )
                }
            </div>
        </div>
    )
}

let mapState = ({userInfo, userSessionCredential}) => ({ 
  userInfo,
  userSessionCredential
});

let mapDispatch = (dispatch) => ({
    joinMeeting: () => {
        dispatch( setCurrentView(MeetingViews.Join));
    },
    signIn: () => {
        window.location.href = getSigninPath();
    },
    startMeeting: () => {
        window.location.href = getStartMeetingPath();
    },
    logout: () => {
      dispatch(setUserSessionCredential(""));
      dispatch(setUserInfo(null));
    }
})

export default connect(mapState, mapDispatch)(
  Home
);
