
export function post(url, headers, body) {
    return window.fetch(url, {
        method: "POST",
        headers: headers,
        cache: "default",
        body: body
    })
}

export function get(url, init) {
    return window.fetch(url, {
        method: 'GET',
        credentials: 'same-origin',
        ...init
    })
}