import React, { useState, useEffect } from 'react';
import './App.scss';
import { connect } from 'react-redux';
import JoinMeeting from "./JoinMeeting";
import Home from './Home';
import Modal from "components/Modal";
import {
  MeetingViews
} from '../store/reducer';
import { 
  getUserInfo
} from "../store/thunk-actions";

function App(props) {
    // let [visible, setVisible] = useState(false);
    let [isFetchingUserinfo, setIsFetchingUserinfo] = useState(false);
    let {
      meetingCurrentView,
      userSessionCredential,
      userInfo,
      dispatch
    } = props;
    
    useEffect( () => {
      console.log(userSessionCredential, userInfo);
      if(userSessionCredential && !userInfo) {
        // fetch user info
        // setIsFetchingUserinfo(true);
        dispatch( getUserInfo())
        // .then(
        //   () => { setIsFetchingUserinfo(false) },
        //   () => { setIsFetchingUserinfo(false) }
        // );
      }
    }, [userSessionCredential, userInfo, dispatch]);
    return (
        <>
            <div className="app">
              { meetingCurrentView === MeetingViews.Join && <JoinMeeting /> }
              { meetingCurrentView === MeetingViews.Home && <Home /> }
            </div>
            {/* <Modal
                visible={visible}
                onOk={() => { setVisible(false)}}
                onCancel={() => { setVisible(false)}}
            >
               <p>Content has been updated, would you like to reload the page?</p>
            </Modal> */}
        </>
    );
}

const mapStateToProps = ({meetingCurrentView, userSessionCredential, userInfo}) => {
  return {
    meetingCurrentView,
    userSessionCredential,
    userInfo
  }
}

export default connect(
  mapStateToProps,
)(App);

