import { get } from "./http"
const url_prefix = process.env.NODE_ENV === "production" ? "" : "/api";

export function getUserInfo() {
    let url = url_prefix + "/userinfo";
    let headers = {
      "Custom-X": "custom-xxx",
    };
    return get(url, {
      headers: headers
    }).then( (response) => {
      return response.json();
    });
}

export function test_reddit_api() {
    return get(`${url_prefix}/cpp.json`);
}

