import { getDomain } from '../utils';
export const SessionKey = "_zm_kms"; // this is set by web server
const PwaIdentifier = "pwa";
const SigninWebPath = `/signin?from=${PwaIdentifier}`;
const StartMeetingPath = `/start/videomeeting?from=${PwaIdentifier}`;

export const getSigninPath = () => {
  return `${getDomain(window.location.href)}${SigninWebPath}`;
}

export const getStartMeetingPath = () => {
  return `${getDomain(window.location.href)}${StartMeetingPath}`;
}

export const getJoinMeetingPath = (meetingNumber, params) => {
  let path = `${getDomain(window.location.href)}/wc/${meetingNumber}/join`;
  let search = [];
  for(let key in params) {
    if(params.hasOwnProperty(key)) {
      search.push(`${key}=${params[key]}`);
    }
  }
  search = search.join("&");
  if(search.length > 0) {
    search = "?" + search;
  }
  return `${path}${search}`;
}